            'https://thirdwx.qlogo.cn/mmopen/vi_32/Q0j4TwGTfTJsfw4BEgchyGX3pvz8YUDBzvDI3X0FYwzgkbwClAl8bpthqDS8WYPPndf4gKQ78d80wGbQlRls7w/132',
          nickName: '想个简单的名字太难了',
          no_num: 2,
          score: '131290',
          sort: 1,
          userId: 11271,
        },
      ],
      playAnimation: false,
      show: false,
      helpLoading: false,
      helperTop: 0,
      hasMore: true,
      loadImagePath: '',
      showItem: 1,
      Audio: null,
      context: null,
      sharePath: 'https://static.roi-cloud.com/base/share_bg.png',
      kingPrize: null,
      phone: '',
      verifyCode: '',
      verifyCodeText: '发送验证码',
      verifyCodeTime: 0,
      codeError: '',
      timer: null,
      verifyCodeResult: {},
    }
  },
  onShow() {
    this.getData()
  },
  onReady() {
    this.context = uni.createCanvasContext('shareCanvas', this)
  },
  onLoad(options) {
    if (options.scene) {
      let params = unescape(options.scene).split(',')
      options.userId = params[1]
      options.gameId = params[0]
      console.log(params, options)
    }
    if (options.gameId) {
      this.$storage.set('gameId', options.gameId)
      this.gameId = options.gameId
    }
    let src = 'https://static.roi-cloud.com/base/bosound_0916.mp3'
    //实例化声音
    this.Audio = uni.createInnerAudioContext()
    this.Audio.src = src //音频地址
    this.user_info = this.$storage.getUser()
    this.loadOptions = options
    if (options.userId) {
      verifyGameHelp({
        gameId: options.gameId,
        hash: options.hash,
        userId: options.userId,
        type: options.type || 0,
      }).then((res) => {
        this.helperInfo = res
        this.$refs.assistance.show()
      })
    }
    this.navbarHeight =
      getApp().globalData.statusBarHeight + getApp().globalData.navBarHeight
  },

  onHide() {
    this.$refs.help.close()
    this.currentHelpItem = 1
  },
  methods: {
    changeVerifyCode: function (e) {
      this.verifyCode = e.detail.value
    },
    savePhone: function () {
      if (!this.phone) {
        this.phoneError = '请填写手机号'
        return false
      } else if (!validPhone(this.phone)) {
        this.phoneError = '手机号格式错误'
        return false
      } else {
        this.phoneError = ''
      }
      if (!this.verifyCode) {
        this.codeError = '请填写验证码'
        return false
      } else {
        this.codeError = ''
      }
      let params = {
        phone: this.phone,
        verifyCode: this.verifyCode,
        time: this.verifyCodeResult.time,
        hash: this.verifyCodeResult.hash,
      }
      this.$loading.show()
      updateUserPhone(params)
        .then((response) => {
          clearInterval(this.timer)
          this.user_info = {
            ...this.user_info,
            phone: this.phone,
          }
          // let user = this.$storage.getUser();
          // this.$storage.setUser({
          // 	...user,
          // 	phone: this.phone
          // });
          this.$refs.dialog.close()
          this.$loading.hide()
        })
        .catch((error) => {
          this.$loading.hide()
          this.$toast.error(error.msg || '短信验证码验证失败')
        })
    },
    changePhone: function (e) {
      this.phone = e.detail.value
    },
    sendCode: function () {
      if (this.verifyCodeTime === 0) {
        if (!this.phone) {
          this.phoneError = '请填写手机号'
          return false
        } else if (!validPhone(this.phone)) {
          this.phoneError = '手机号格式错误'
          return false
        } else {
          this.phoneError = ''
        }
        let params = {
          phone: this.phone,
        }
        smsBindUserVerifyCode(params).then((response) => {
          this.verifyCodeResult = response
          let code = 60
          this.verifyCodeTime = code
          this.verifyCodeText = code
          this.timer = setInterval(() => {
            code = code - 1
            this.verifyCodeTime = code
            if (code <= 0) {
              this.verifyCodeTime = 0
              this.verifyCodeText = '重新发送'
              clearInterval(this.timer)
            } else {
              this.verifyCodeText = code
            }
          }, 1000)
        })
      }
    },
    onClose: function () {
      clearInterval(this.timer)
      this.$refs.dialog.close()
    },
    checkPhone() {
      this.$loading.show()
      userInfo().then((res) => {
        this.$loading.hide()
        if (res.phone) {
          this.$refs.over_popup.close()
        } else {
          this.phone = ''
          this.verifyCode = ''
          this.verifyCodeText = '发送验证码'
          this.verifyCodeTime = 0
          this.phoneError = ''
          this.codeError = ''
          this.agreeError = ''
          this.$refs.dialog.open()
        }
      })
    },
    getAdvertList() {
      let params = {}
      if (this.gameId) {
        params.gameId = this.gameId
      }
      apiGetAdvert(params).then((res) => {
        this.advertList = res
      })
    },
    onHelper: function () {
      this.helpTop = 0
      this.hasMore = true
      this.show = true
      this.popShow('help')
    },
    getKingOfKingPrize() {
      apiKingOfKingPrize({
        gameId: this.gameId,
      }).then((res) => {
        this.kingPrize = res
        this.$refs.assistance.hide()
        this.$refs.over_popup.show()
      })
    },
    closeHelp: function () {
      this.$refs.help.close()
    },
    helpMeListMore() {
      if (!this.helpLoading && this.hasMore) {
        this.getHelperList(this.helper.page + 1)
      }
    },
    helpScroll: function (e) {
      this.helpTop = e.detail.scrollTop
    },
    closeThisPage() {
      uni.switchTab({
        url: '/pages/sponsor/sponsor',
      })
    },
    cancelLogin() {
      this.$refs.login_popup.close()
    },
    openProtocol() {
      uni.downloadFile({
        url: 'https://static.roi-cloud.com/base/protocol.pdf',
        success(res) {
          let filePath = res.tempFilePath
          uni.openDocument({
            filePath: filePath,
            fileType: 'pdf',
            success(res) {
              // console.log("打开文档成功");
            },
            fail(res) {
              console.log(res)
            },
            complete() {},
          })
        },
      })
    },
    getData() {
      //   apiGetMinSetting().then((res) => {
      //     this.setting = res
      //     this.gameId = this.$storage.get('gameId')
      //     if (JSON.stringify(this.$storage.getUser()) == '{}') {
      //       // this.$refs.login_popup.open('bottom')
      //       this.getAdvertList()
      //     } else if (this.gameId == '') {
      //       this.$refs.login_popup.close()
      //       this.$storage.set('gameId', res.DEFAULT.GAME_ID)
      //       this.gameId = res.DEFAULT.GAME_ID
      //       this.initData()
      //     } else {
      //       this.initData()
      //     }
      //   })
    },
    // 初始化
    initData() {
      this.$refs.login_popup.close()
      this.user_info = this.$storage.getUser()
      this.getUserPlayInfo()
      this.getGameInfo()
      this.getAdvertList()
      // 助力记录
      this.getHelperList(1)
    },
    openLocationSetting() {
      uni.openSetting({
        success: (res) => {
          if (res.authSetting['scope.userLocation']) {
            this.$refs.location.hide()
          }
        },
        fail(err) {},
      })
    },
    onVip: function () {
      uni.navigateTo({
        url: '/pages/vip/vip',
      })
    },
    cancel: function () {
      this.$refs.popup.close()
    },
    changeItem(type) {
      this.currentScoreItem = type
    },
    toCreate() {
      statisticsAllGameList().then((res) => {
        let list = res.list
        let _this = this
        if (list.length > 0) {
          this.$storage.set('gameId', res.list[0].gameId)
          this.gameId = res.list[0].gameId
          this.initData()
        } else {
          uni.showModal({
            content: '请先创建游戏',
            confirmText: '确定',
            success: function (res) {
              if (res.confirm) {
                uni.navigateTo({
                  url: '/pages/sponsor/sponsorSetting',
                })
              } else {
                _this.closeThisPage()
              }
            },
          })
        }
      })
    },
    toPage(e) {
      if (JSON.stringify(this.$storage.getUser()) == '{}') {
        this.$refs.login_popup.open()
      } else {
        uni.navigateTo({
          url: e.currentTarget.dataset.url,
        })
      }
    },
    getImageInfo(url) {
      return new Promise((reslove, reject) => {
        uni.getImageInfo({
          src: url,
          success: (res) => {
            reslove(res)
          },
          fail(error) {
            reject(error)
          },
        })
      })
    },
    createNewImg: async function (gameInfo) {
      let res = await this.getImageInfo(
        'https://static.roi-cloud.com/base/share_bg.png'
      )
      await this.context.drawImage(res.path, 0, 0, 260, 208)
      if (gameInfo.logoInfo && gameInfo.logoInfo.img) {
        let logoInfo = await this.getImageInfo(gameInfo.logoInfo.img)
        this.context.drawImage(
          logoInfo.path,
          10,
          10,
          100,
          (100 / logoInfo.width) * logoInfo.height
        )
      }
      await this.context.draw()
      setTimeout(() => {
        uni.canvasToTempFilePath({
          canvasId: 'shareCanvas',
          success: (toImage) => {
            this.sharePath = toImage.tempFilePath
          },
          fail: function () {
            //TODO
          },
        })
      }, 300)
    },
    getAward() {
      this.$loading.show()
      gameAward({
        gameId: this.gameId,
      }).then((res) => {
        for (let index in res.list) {
          res.list[index].playTime = moment(
            res.list[index].playTime * 1000
          ).format('YYYY.MM.DD')
        }
        this.scoreDetailList = res.list
        this.$loading.hide()
      })
      this.$loading.show()
      exchangeGame({
        gameId: this.gameId,
      }).then((res) => {
        for (let index in res.list) {
          res.list[index].verify_time = moment(
            res.list[index].verifyTime * 1000
          ).format('YYYY.MM.DD')
        }
        this.exchangeList = res.list
        this.$loading.hide()
      })
    },
    checkLogin() {
      let flag = true
      if (JSON.stringify(this.user_info) == '{}') {
        this.$refs.login_popup.open('bottom')
        flag = false
      }
      return flag
    },
    gameHelp() {
      let params = {
        gameId: this.gameId,
        userId: this.loadOptions.userId,
        hash: this.loadOptions.hash,
      }
      if (JSON.stringify(this.user_info) == '{}') {
        this.toLogin()
      } else {
        addGameHelp(params)
          .then((res) => {
            this.getUserPlayInfo()
            this.getHelperList(1)
            this.getAward()
            this.$refs.assistance.hide()
            this.$refs.help_other.show()
          })
          .catch((error) => {
            this.$refs.assistance.hide()
            this.helpFaileMsg = error.msg
            this.$refs.help_other_faile.show()
          })
      }
    },
    changeHelpTypeList(type) {
      this.hasMore = true
      this.currentHelpItem = type
      // this.helpTop = 0;
      this.getHelperList(1, true, type)
    },
    play() {
      uni.getNetworkType({
        success: (res) => {
          if (res.networkType === 'none') {
            this.$refs.network.show()
          } else {
            if (this.playLoading) {
              return
            }
            this.playLoading = true
            if (JSON.stringify(this.$storage.getUser()) == '{}') {
              this.playLoading = false
              this.toLogin()
            } else {
              if (this.$storage.get('getLocationTime') == '') {
                this.getSetting()
                return
              } else {
                let get_time = this.$storage.get('getLocationTime').get_time
                let now = new Date().getTime()
                if ((now - get_time) / 1000 / 60 / 60 > 3) {
                  this.getSetting()
                  return
                }
                this.addPlay()
              }
            }
          }
        },
      })
    },
    playSound() {
      this.Audio.seek(0.1)
      this.Audio.play() //执行播放
    },
    toLogin() {
      this.$refs.login_popup.open('bottom')
    },
    addPlay() {
      try {
        if (this.userPlayInfo.playTimes - this.userPlayInfo.playedTimes == 0) {
          this.playLoading = false
          this.$refs.share.show()
          // this.$refs.award.show()
          this.$loading.hide()
          return
        }
        this.$loading.show()
        this.playLoading = true
        addUserGameRecord({
          agreeType: 1,
          gameId: this.gameId,
        })
          .then((res) => {
            let arr = res.gameResult.split(',')
            this.pointsImages = []
            for (let index in arr) {
              this.pointsImages.push(
                '/static/images/icon_dice-' + arr[index] + '.png'
              )
            }
            this.gameResult = res
            this.$loading.hide()
            this.playSound()
            this.playAnimation = true
            this.timer = setTimeout(() => {
              clearTimeout(this.timer)
              this.$refs.award.show()
              this.getUserPlayInfo()
              this.getGameInfo()
              setTimeout(() => {
                this.playLoading = false
              }, 500)
              this.playAnimation = false
            }, 1200)
          })
          .catch((error) => {
            this.$loading.hide()
            /* 超过人数 */
            if (startsWith(error.msg, '该游戏人数已经达到上限')) {
              this.$refs.tip.show()
            } else if (startsWith(error.msg, '该游戏指定参与范围')) {
              this.positionMsg = error.msg
              this.$refs.position.show()
            } else if (startsWith(error.msg, '请授权位置进行博饼')) {
              this.$refs.location.show()
            } else {
              this.$toast.error(error.msg)
            }
            this.playLoading = false
          })
        uni.requestSubscribeMessage({
          tmplIds: [
            '7d-RFgHEirGnN77ZbgGZU5vGfHIUeBpX8hwM8GCEXbM',
            'mVo4jCV2RqAOb4nZdBjCOfXyTbQs5L3F04XBbEVJroc',
          ],
          success: (subscribeRes) => {},
          fail: (res) => {
            console.log(res)
          },
        })
      } catch (e) {
        console.log(e)
      }
    },
    updateLocation(res) {
      res.get_time = new Date().getTime()
      this.$storage.set('getLocationTime', res)
      this.$loading.show()
      apiSetUserLocation({
        longitude: res.longitude,
        latitude: res.latitude,
      }).then((res) => {
        this.playLoading = false
        this.addPlay()
        this.$loading.hide()
      })
    },
    getSetting() {
      const that = this
      uni.getSetting({
        success: (res) => {
          if (res.authSetting['scope.userLocation']) {
            uni.getLocation({
              type: 'gcj02',
              altitude: true,
              success(res) {
                that.updateLocation(res)
              },
            })
          } else {
            that.getAuthorize()
          }
        },
      })
    },
    // 用户授权
    getAuthorize() {
      const that = this
      uni.authorize({
        scope: 'scope.userLocation',
        success(res) {
          uni.getLocation({
            type: 'gcj02',
            altitude: true,
            success(res) {
              console.log(3)
              that.updateLocation(res)
            },
          })
        },
        // 授权失败
        fail: (err) => {
          console.log(4)
          this.playLoading = false
          this.$refs.location.show()
        },
      })
    },
    closeThisPage() {
      uni.switchTab({
        url: '/pages/sponsor/sponsor',
      })
    },
    popShow(ref) {
      if (this.checkLogin()) {
        this.$refs[ref].show()
      }
    },
    getUserScore() {
      this.$loading.show()
      gameKingOfKingsRankScore({
        gameId: this.gameId,
      }).then((res) => {
        this.userRank = res
        this.$loading.hide()
      })
    },
    getRankScore() {
      gameKingOfKingsList({
        gameId: this.gameId,
      }).then((kingList) => {
        this.kingRankList = kingList
        for (let index in kingList) {
          kingList[index].no_num = parseInt(index) + 1
        }
        gameKingofKingsPrizeList({
          gameId: this.gameId,
        }).then((res1) => {
          let kingofKingsList = []
          let num = 1
          for (let index in res1) {
            let item = {
              info: res1[index],
              range:
                res1[index].prizeNum == 1
                  ? '第' + num + '名'
                  : '第' + num + '～' + (num + res1[index].prizeNum - 1) + '名',
              list: kingList.slice(0, res1[index].prizeNum),
            }
            num += res1[index].prizeNum
            kingList.splice(0, res1[index].prizeNum)
            kingofKingsList.push(item)
          }
          this.kingofKingsList = kingofKingsList
          this.otherKingList = kingList
        })
      })
    },
    getUserProfile(e) {
      if (this.logining == true) {
        return
      }
      // 推荐使用uni.getUserProfile获取用户信息，开发者每次通过该接口获取用户个人信息均需用户确认
      // 开发者妥善保管用户快速填写的头像昵称，避免重复弹窗
      this.logining = true
      uni.getUserProfile({
        desc: '用于完善会员资料', // 声明获取用户个人信息后的用途，后续会展示在弹窗中，请谨慎填写
        success: (res) => {
          console.log(res)
          let params = {
            avatarUrl: res.userInfo.avatarUrl,
            nickName: res.userInfo.nickName,
          }
          this.$loading.show()
          uni.login({
            success: (result) => {
              this.wxLogin({
                code: result.code,
                ...params,
              })
            },
            fail: () => {
              this.logining = false
            },
          })
        },
        fail: (res) => {
          this.logining = false
        },
      })
    },
    getHelperList(page) {
      this.$loading.show()
      this.helpLoading = true
      userHelpRecordList({
        gameId: this.gameId,
        type: this.currentHelpItem,
        pageSize: this.helper.pageSize,
        page: page,
      })
        .then((res) => {
          let list = res.list || []
          if (list.length < this.helper.pageSize) {
            this.hasMore = false
          }
          if (page !== 1) {
            list = [...this.helper.list, ...list]
          }
          this.helper = {
            ...this.helper,
            list: list,
            page: page,
          }
          this.helpLoading = false
          this.$loading.hide()
        })
        .catch(() => {
          this.helpLoading = false
          this.$loading.hide()
        })
    },
    onGameManager: function () {
      this.$refs.popup.open()
    },
    wxLogin(wxData) {
      login(wxData)
        .then((res) => {
          this.logining = false
          this.user_info = res
          this.$storage.setUser(res)
          this.$loading.hide()
          this.canClose = true
          this.getData()
        })
        .catch((res) => {
          this.logining = false
          loading.hide()
        })
    },
    getUserPlayInfo() {
      userGame({
        gameId: this.gameId,
      }).then((res) => {
        this.userPlayInfo = res
      })
    },
    getGameInfo() {
      apiGetGameInfo({
        gameId: this.gameId,
      })
        .then((res) => {
          res.startTime = moment(res.startTime * 1000).format('MM月DD日 HH:mm')
          res.overChance = moment(res.redeemEndTime * 1000).format(
            'MM月DD日 HH:mm'
          )
          res.endTime = moment(res.endTime * 1000).format('MM月DD日 HH:mm')
          this.gameInfo = res
          if (res.isSvip == 1 && this.user_info) {
            // 用户排名
            this.getUserScore()
          }
          this.createNewImg(res)
          if (res.isSvip == 1) {
            // 王中王排名
            this.getRankScore()
          }
          this.getAward() //获取中奖明细
          // 游戏待设置
          if (res.status == 'game_creat') {
            uni.showModal({
              content: '游戏待设置',
              confirmText: '确定',
              showCancel: false,
              success: (res) => {
                if (res.confirm) {
                  this.closeThisPage()
                }
              },
            })
          }
          // 游戏下架
          if (res.status == 'game_off') {
            this.$refs.get_out.show()
          }

          // 游戏结束

          if (res.status == 'game_end' && res.isSvip == 1 && this.user_info) {
            this.getKingOfKingPrize()
          } else if (res.status == 'game_end') {
            this.$refs.assistance.hide()
            this.$refs.over_popup.show()
          }
        })
        .catch((error) => {
          this.toCreate()
        })
    },
    getTime: function (time, format = 'YYYY-MM-DD') {
      return time ? moment.unix(time).format(format) : ''
    },
  },
  onShareAppMessage(e) {
    let rank = e.target && e.target.dataset ? e.target.dataset.rank : ''
    let type = 0
    if (rank == 1) {
      // 王中王排行分享
      type = 1
    } else if (this.gameResult.level == 0) {
      // 未中奖
      type = 4
    } else if (this.gameInfo.gameType == 2) {
      type = 2
    } else if (this.gameInfo.gameType == 1) {
      type = 3
    }
    return {
      title: `${
        this.gameInfo.gameName ? this.gameInfo.gameName : '一起云博饼'
      }`,
      imageUrl: this.sharePath,
      path: `/pages/index/index?gameId=${this.gameId}&hash=${this.userPlayInfo.hash}&userId=${this.userPlayInfo.userId}&recordId=${this.gameResult.recordId}&type=${type}`,
    }
  },
}
</script>

<style lang="scss">
.diy_logo {
  width: 150px;
  height: 35px;
}

#shareCanvas {
  position: fixed;
  top: -9999999999999px;
  background: #fff;
  width: 260px;
  height: 198px;
  z-index: 999;
}

.ad_wrap {
  width: 580upx;
  height: 140upx;
  margin: 40upx auto;
  overflow: hidden;
  background: linear-gradient(137deg, #95ffdc 0%, #ff8944 99%, #ff8944 100%);
  border-radius: 20upx;

  swiper,
  image,
  .swiper-item {
    width: 100%;
    height: 100%;
  }
}

.contactPoup {
  padding: 40upx;
  text-align: center;

  image {
    padding: 40upx;
    width: 400upx;
    height: 400upx;
  }
}

.no_data {
  padding: 40upx 0;
  text-align: center;
  color: #aaa;
  font-size: 30upx;
}

.bottom-button-wrap {
  position: fixed;
  background: #e6e6e6;
  z-index: 10;
  width: 100%;
  height: 50px;
  left: 0;
