#main {
  min-height: 100vh;
  box-sizing: border-box;
  overflow: hidden;

  .tips {
    color: #fff;
    text-align: center;
    font-size: 24upx;
    margin: 18upx 0 8upx;
  }

  .bowl_title {
    width: 580upx;
    height: 350upx;
    margin: 0 auto;
    position: relative;

    image {
      width: 100%;
      height: 100%;
    }
  }

  .bowl {
    width: 100%;
    height: 738upx;
    position: relative;

    .rule {
      height: 56upx;
      line-height: 56upx;
      padding: 0 30upx;
      color: #fff;
      position: absolute;
      right: 36upx;
      top: 20upx;
      font-size: 24upx;
      border-radius: 56upx;
      background: rgba($color: #000000, $alpha: 0.3);
    }

    .dice_wrap {
      position: absolute;
      width: 68%;
      height: 57%;
      top: 21%;
      left: 14%;

      .dice {
        width: 70upx;
        height: 70upx;
        transform: rotate(360deg);

        &.playAnimation {
          opacity: 0;
          animation: diceAnimate 0.4s forwards linear;
          transform: translate(0, -800%) rotate(0deg) scale(0.8);
        }

        &.playAnimation:nth-child(1) {
          animation-delay: 0.2s;
        }

        &:nth-child(1) {
          position: absolute;
          left: 160upx;
          top: 113upx;
          animation-delay: 0.2s;
        }

        &.playAnimation:nth-child(2) {
          animation-delay: 0.3s;
          animation: diceAnimate1 0.4s forwards linear;
        }

        &:nth-child(2) {
          position: absolute;
          left: 310upx;
          top: 110upx;
        }

        &.playAnimation:nth-child(3) {
          animation-delay: 0.2s;
        }

        &:nth-child(3) {
          position: absolute;
          left: 120upx;
          top: 240upx;
        }

        &.playAnimation:nth-child(4) {
          animation-delay: 0.1s;
          animation: diceAnimate1 0.6s forwards linear;
        }

        &:nth-child(4) {
          position: absolute;
          left: 210upx;
          top: 330upx;
        }

        &.playAnimation:nth-child(5) {
          animation-delay: 0.3s;
        }

        &:nth-child(5) {
          position: absolute;
          left: 350upx;
          top: 250upx;
        }

        &.playAnimation:nth-child(6) {
          animation-delay: 0.1s;
        }

        &:nth-child(6) {
          position: absolute;
          left: 240upx;
          top: 230upx;
        }
      }
    }

    .icon_bowl {
      width: 100%;
      height: 100%;
    }
  }

  .gift_wrap {
    position: absolute;
    right: 10upx;
    width: 106upx;
    top: -30upx;
    z-index: 99;

    .icon_gift {
      width: 134upx;
      height: 146upx;
      position: absolute;
      left: 50%;
      top: -46%;
      transform: translateX(-50%);
      z-index: 1;
    }

    .shadow {
      width: 106upx;
      height: 106upx;
      line-height: 150upx;
      color: #fff;
      font-size: 20upx;
      text-align: center;
      background: linear-gradient(180deg, #c872d7 0%, #a031d6 100%);
      border-radius: 21upx;
    }
  }

  .chance {
    display: flex;
    align-items: center;
    color: #fff;
    justify-content: center;
    margin-top: 20upx;

    .number {
      background: #b13737;
